import React, { useRef } from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { User, Code, Palette, Zap, Target, Award, Users, Clock, Star, TrendingUp } from 'lucide-react';

interface AboutProps {
  onSectionChange: (section: string) => void;
}

const About: React.FC<AboutProps> = ({ onSectionChange }) => {
  const sectionRef = useRef<HTMLElement>(null);
  const { scrollYProgress } = useScroll({
    target: sectionRef,
    offset: ["start end", "end start"]
  });

  const y = useTransform(scrollYProgress, [0, 1], ["0%", "50%"]);
  const opacity = useTransform(scrollYProgress, [0, 1], [1, 0]);

  const features = [
    {
      icon: Code,
      title: "Frontend Development",
      description: "Expert in React, Next.js, and modern JavaScript frameworks"
    },
    {
      icon: Palette,
      title: "UI/UX Design",
      description: "Creating intuitive and beautiful user experiences"
    },
    {
      icon: Zap,
      title: "Performance Optimization",
      description: "Building fast, scalable, and efficient applications"
    },
    {
      icon: Target,
      title: "Problem Solving",
      description: "Analytical approach to complex technical challenges"
    }
  ];

  const stats = [
    { icon: Users, value: "50+", label: "Projects Completed" },
    { icon: Clock, value: "3+", label: "Years Experience" },
    { icon: Star, value: "98%", label: "Client Satisfaction" },
    { icon: TrendingUp, value: "100%", label: "On-Time Delivery" }
  ];

  return (
    <section
      ref={sectionRef}
      id="about"
      className="py-20 relative overflow-hidden"
    >
      {/* Background */}
      <div className="absolute inset-0 professional-bg opacity-20"></div>
      
      <div className="relative z-10 max-w-7xl mx-auto px-6">
        {/* Header */}
        <motion.div
          style={{ y, opacity }}
          className="text-center mb-16"
        >
          <motion.h2
            initial={{ opacity: 0, y: 30 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8 }}
            viewport={{ once: true }}
            className="text-4xl md:text-5xl font-bold mb-6 text-neutral-800"
          >
            About <span className="professional-text">Me</span>
          </motion.h2>
          <motion.div
            initial={{ opacity: 0, scale: 0.8 }}
            whileInView={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            viewport={{ once: true }}
            className="w-24 h-1 bg-gradient-to-r from-primary-500 to-secondary-500 mx-auto rounded-full"
          ></motion.div>
        </motion.div>

        {/* Main Content */}
        <div className="grid lg:grid-cols-2 gap-16 items-center mb-20">
          {/* Personal Story */}
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8 }}
            viewport={{ once: true }}
          >
            <h3 className="text-2xl font-bold mb-6 text-neutral-800">
              My Journey in Tech
            </h3>
            <div className="space-y-4 text-neutral-600 leading-relaxed">
              <p>
                I'm a passionate Frontend Developer and UI/UX Designer with over 3 years of experience 
                creating exceptional digital experiences. My journey began with a curiosity about how 
                technology can solve real-world problems and make people's lives easier.
              </p>
              <p>
                I specialize in building modern, responsive web applications using React, Next.js, 
                and cutting-edge technologies. My approach combines technical expertise with a deep 
                understanding of user experience principles.
              </p>
              <p>
                When I'm not coding, you'll find me exploring new design trends, contributing to 
                open-source projects, or mentoring junior developers. I believe in continuous learning 
                and staying ahead of industry trends.
              </p>
            </div>
            
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => onSectionChange('contact')}
              className="mt-8 professional-button"
            >
              Let's Work Together
            </motion.button>
          </motion.div>

          {/* Profile Image */}
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            viewport={{ once: true }}
            className="relative"
          >
            <div className="w-80 h-80 mx-auto bg-gradient-to-br from-primary-100 to-secondary-100 rounded-full flex items-center justify-center shadow-2xl">
              <div className="w-72 h-72 bg-gradient-to-br from-primary-200 to-secondary-200 rounded-full flex items-center justify-center">
                <div className="w-64 h-64 bg-gradient-to-br from-primary-300 to-secondary-300 rounded-full flex items-center justify-center">
                  <User size={120} className="text-primary-600" />
                </div>
              </div>
            </div>
            
            {/* Floating Elements */}
            <div className="absolute -top-4 -right-4 w-16 h-16 bg-accent-100 rounded-full flex items-center justify-center shadow-lg">
              <Code size={24} className="text-accent-600" />
            </div>
            <div className="absolute -bottom-4 -left-4 w-16 h-16 bg-secondary-100 rounded-full flex items-center justify-center shadow-lg">
              <Palette size={24} className="text-secondary-600" />
            </div>
          </motion.div>
        </div>

        {/* Key Features */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="mb-20"
        >
          <h3 className="text-3xl font-bold text-center mb-12 text-neutral-800">
            What I <span className="professional-text">Bring</span> to the Table
          </h3>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {features.map((feature, index) => {
              const Icon = feature.icon;
              return (
                <motion.div
                  key={feature.title}
                  initial={{ opacity: 0, y: 30 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  transition={{ duration: 0.6, delay: index * 0.1 }}
                  viewport={{ once: true }}
                  className="professional-card p-6 text-center hover:scale-105 transition-transform duration-300"
                >
                  <div className="w-16 h-16 bg-gradient-to-br from-primary-100 to-secondary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <Icon size={28} className="text-primary-600" />
                  </div>
                  <h4 className="text-xl font-semibold mb-3 text-neutral-800">
                    {feature.title}
                  </h4>
                  <p className="text-neutral-600 leading-relaxed">
                    {feature.description}
                  </p>
                </motion.div>
              );
            })}
          </div>
        </motion.div>

        {/* Statistics */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center"
        >
          <h3 className="text-3xl font-bold mb-12 text-neutral-800">
            My <span className="professional-text">Achievements</span>
          </h3>
          
          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {stats.map((stat, index) => {
              const Icon = stat.icon;
              return (
                <motion.div
                  key={stat.label}
                  initial={{ opacity: 0, scale: 0.8 }}
                  whileInView={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.6, delay: index * 0.1 }}
                  viewport={{ once: true }}
                  className="professional-card p-6"
                >
                  <div className="w-16 h-16 bg-gradient-to-br from-primary-100 to-secondary-100 rounded-full flex items-center justify-center mx-auto mb-4">
                    <Icon size={28} className="text-primary-600" />
                  </div>
                  <div className="text-3xl font-bold mb-2 professional-text">
                    {stat.value}
                  </div>
                  <div className="text-neutral-600 font-medium">
                    {stat.label}
                  </div>
                </motion.div>
              );
            })}
          </div>
        </motion.div>

        {/* Call to Action */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mt-16"
        >
          <div className="professional-highlight max-w-4xl mx-auto">
            <h4 className="text-2xl font-bold mb-4 text-neutral-800">
              Ready to Start Your Next Project?
            </h4>
            <p className="text-neutral-600 mb-6">
              Let's collaborate to bring your vision to life with cutting-edge technology and 
              exceptional design.
            </p>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => onSectionChange('contact')}
              className="professional-button mr-4"
            >
              Get Started
            </motion.button>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              onClick={() => onSectionChange('projects')}
              className="professional-button-outline"
            >
              View Portfolio
            </motion.button>
          </div>
        </motion.div>
      </div>
    </section>
  );
};

export default About;
