import React, { useRef } from 'react';
import { motion, useScroll, useTransform } from 'framer-motion';
import { Briefcase, Calendar, MapPin, ExternalLink } from 'lucide-react';

interface ExperienceProps {
  onSectionChange: (section: string) => void;
}

const Experience: React.FC<ExperienceProps> = ({ onSectionChange }) => {
  const sectionRef = useRef<HTMLElement>(null);
  const { scrollYProgress } = useScroll({
    target: sectionRef,
    offset: ["start end", "end start"]
  });

  const y = useTransform(scrollYProgress, [0, 1], ["0%", "50%"]);
  const opacity = useTransform(scrollYProgress, [0, 1], [1, 0]);

  const experiences = [
    {
      id: 1,
      title: "Senior Frontend Developer",
      company: "TechCorp Solutions",
      location: "San Francisco, CA",
      period: "2022 - Present",
      description: "Leading frontend development for enterprise applications, mentoring junior developers, and implementing best practices for scalable React applications.",
      achievements: [
        "Led development of 5+ major features used by 100K+ users",
        "Improved application performance by 40% through optimization",
        "Mentored 3 junior developers and conducted code reviews"
      ],
      technologies: ["React", "TypeScript", "Next.js", "Tailwind CSS", "GraphQL"]
    },
    {
      id: 2,
      title: "Frontend Developer",
      company: "Digital Innovations Inc",
      location: "New York, NY",
      period: "2020 - 2022",
      description: "Developed responsive web applications and collaborated with design teams to create seamless user experiences.",
      achievements: [
        "Built 10+ client websites with 99.9% uptime",
        "Implemented CI/CD pipelines reducing deployment time by 60%",
        "Collaborated with UX designers to improve conversion rates by 25%"
      ],
      technologies: ["React", "JavaScript", "CSS3", "Webpack", "Git"]
    },
    {
      id: 3,
      title: "Junior Developer",
      company: "StartUp Ventures",
      location: "Austin, TX",
      period: "2019 - 2020",
      description: "Started my journey in web development, learning modern frameworks and contributing to various projects.",
      achievements: [
        "Contributed to 3 successful product launches",
        "Learned modern JavaScript frameworks and best practices",
        "Participated in agile development processes"
      ],
      technologies: ["HTML5", "CSS3", "JavaScript", "jQuery", "Bootstrap"]
    }
  ];

  const skills = [
    { name: "Frontend Development", level: 95, color: "#a855f7" },
    { name: "React Ecosystem", level: 90, color: "#ec4899" },
    { name: "TypeScript", level: 88, color: "#06b6d4" },
    { name: "UI/UX Design", level: 85, color: "#10b981" },
    { name: "Performance Optimization", level: 82, color: "#f59e0b" },
    { name: "Testing & Quality", level: 80, color: "#ef4444" }
  ];

  return (
    <section
      id="experience"
      ref={sectionRef}
      className="relative py-20 px-4 overflow-hidden"
    >
      {/* Background Effects */}
      <div className="absolute inset-0 bg-gradient-to-br from-dark-800/50 to-dark-900/50" />
      <div className="absolute inset-0 cyber-grid opacity-10" />
      
      <motion.div
        style={{ y, opacity }}
        className="relative z-10 max-w-7xl mx-auto"
      >
        {/* Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-6xl font-bold mb-6">
            <span className="gradient-text neon-text">Professional</span>
            <span className="text-white"> Experience</span>
          </h2>
          <p className="text-xl text-gray-300 max-w-3xl mx-auto leading-relaxed">
            A journey of continuous growth and learning, working with amazing teams 
            to create impactful digital solutions.
          </p>
        </motion.div>

        {/* Experience Timeline */}
        <div className="grid lg:grid-cols-2 gap-16 items-start">
          {/* Left Column - Timeline */}
          <motion.div
            initial={{ opacity: 0, x: -50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.2 }}
            viewport={{ once: true }}
            className="space-y-8"
          >
            <h3 className="text-3xl font-bold text-white mb-8">
              Career <span className="neon-text neon-cyan">Journey</span>
            </h3>
            
            {experiences.map((experience, index) => (
              <motion.div
                key={experience.id}
                initial={{ opacity: 0, y: 30 }}
                whileInView={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.4 + index * 0.1 }}
                viewport={{ once: true }}
                className="relative"
              >
                {/* Timeline Line */}
                {index < experiences.length - 1 && (
                  <div className="absolute left-6 top-16 bottom-0 w-0.5 bg-gradient-to-b from-neon-purple to-transparent" />
                )}
                
                {/* Timeline Dot */}
                <div className="absolute left-4 top-6 w-4 h-4 bg-gradient-to-r from-neon-purple to-neon-pink rounded-full border-4 border-dark-800" />
                
                {/* Content */}
                <div className="ml-12">
                  <div className="glass-effect p-6 rounded-2xl hover:border-neon-purple/50 transition-all duration-300">
                    <div className="flex items-start justify-between mb-4">
                      <h4 className="text-xl font-bold text-white">{experience.title}</h4>
                      <span className="text-sm text-neon-purple font-medium bg-neon-purple/10 px-3 py-1 rounded-full">
                        {experience.period}
                      </span>
                    </div>
                    
                    <div className="flex items-center space-x-4 mb-3 text-gray-400">
                      <div className="flex items-center space-x-2">
                        <Briefcase size={16} />
                        <span className="text-sm">{experience.company}</span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <MapPin size={16} />
                        <span className="text-sm">{experience.location}</span>
                      </div>
                    </div>
                    
                    <p className="text-gray-300 mb-4 leading-relaxed">
                      {experience.description}
                    </p>
                    
                    {/* Achievements */}
                    <div className="mb-4">
                      <h5 className="text-sm font-semibold text-neon-cyan mb-2">Key Achievements:</h5>
                      <ul className="space-y-1">
                        {experience.achievements.map((achievement, idx) => (
                          <li key={idx} className="text-xs text-gray-400 flex items-start space-x-2">
                            <div className="w-1.5 h-1.5 bg-neon-cyan rounded-full mt-2 flex-shrink-0" />
                            <span>{achievement}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                    
                    {/* Technologies */}
                    <div className="flex flex-wrap gap-2">
                      {experience.technologies.map((tech) => (
                        <span
                          key={tech}
                          className="px-2 py-1 bg-white/5 border border-white/10 rounded text-xs text-gray-300 hover:bg-neon-purple/20 hover:border-neon-purple/50 transition-all duration-300"
                        >
                          {tech}
                        </span>
                      ))}
                    </div>
                  </div>
                </div>
              </motion.div>
            ))}
          </motion.div>

          {/* Right Column - Skills & Stats */}
          <motion.div
            initial={{ opacity: 0, x: 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.4 }}
            viewport={{ once: true }}
            className="space-y-8"
          >
            <h3 className="text-3xl font-bold text-white mb-8">
              Professional <span className="neon-text neon-pink">Skills</span>
            </h3>
            
            {/* Skills Progress */}
            <div className="space-y-6">
              {skills.map((skill, index) => (
                <motion.div
                  key={skill.name}
                  initial={{ opacity: 0, x: 30 }}
                  whileInView={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.6, delay: 0.6 + index * 0.1 }}
                  viewport={{ once: true }}
                  className="space-y-2"
                >
                  <div className="flex justify-between items-center">
                    <span className="text-white font-medium">{skill.name}</span>
                    <span className="text-sm text-gray-400">{skill.level}%</span>
                  </div>
                  
                  <div className="w-full bg-white/10 rounded-full h-2 overflow-hidden">
                    <motion.div
                      initial={{ width: 0 }}
                      whileInView={{ width: `${skill.level}%` }}
                      transition={{ duration: 1, delay: 0.8 + index * 0.1 }}
                      viewport={{ once: true }}
                      className="h-full rounded-full relative"
                      style={{ background: `linear-gradient(90deg, ${skill.color}, ${skill.color}80)` }}
                    >
                      <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent animate-pulse" />
                    </motion.div>
                  </div>
                </motion.div>
              ))}
            </div>

            {/* Stats */}
            <motion.div
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 0.8 }}
              viewport={{ once: true }}
              className="glass-effect p-6 rounded-2xl"
            >
              <h4 className="text-xl font-bold text-white mb-4 text-center">
                Career <span className="neon-text neon-green">Highlights</span>
              </h4>
              <div className="grid grid-cols-2 gap-4">
                <div className="text-center">
                  <div className="text-2xl font-bold gradient-text mb-1">3+</div>
                  <div className="text-sm text-gray-400">Years Experience</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold gradient-text mb-1">50+</div>
                  <div className="text-sm text-gray-400">Projects Completed</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold gradient-text mb-1">15+</div>
                  <div className="text-sm text-gray-400">Happy Clients</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold gradient-text mb-1">100%</div>
                  <div className="text-sm text-gray-400">Client Satisfaction</div>
                </div>
              </div>
            </motion.div>
          </motion.div>
        </div>
      </motion.div>
    </section>
  );
};

export default Experience;
